.speakers-container {
  position: relative;
  display: grid;
  grid-template-rows: auto auto auto auto;
  grid-template-columns: auto auto auto auto;
  justify-content: space-between;
  align-items: stretch;
  margin-top: 4vh;

  //.speaker-filler {
